body {
    margin: 0;
    color: #525f7f;
    background-color: #f8f9fe;
}

.fill-default {
    fill:  #e4e4e4 !important;
    /* #232222; */
}

.separator {
    position: absolute;
    top: auto;
    left: 0;
    right: 0;
    width: 100%;
    height: 150px;
    transform: translateZ(0);
    overflow: hidden;
    pointer-events: none;
}

.separator svg {
    position: absolute;
    pointer-events: none;
}

.separator-top {
    top: 0;
    bottom: auto;
}

.separator-top svg {
    top: 0;
}

.separator-bottom {
    top: auto;
    bottom: 55.3%;
}

.separator-bottom svg {
    bottom: 0;
}

.separator-inverse {
    transform: rotate(180deg);
}

.separator-skew {
    height: 0px;
}

@media (min-width: 1200px) {
    .separator-skew {
        height: 100px;
    }
}

.main-content .navbar-top {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 1;
    background-color: transparent;
    padding-left: 0 !important;
    padding-right: 0 !important;
}

.bg-default {
    background-color: #e4e4e4 !important;
    /* #232222 !important; */
}

.bg-gradient-info {
    background: linear-gradient(87deg, #e72339 0, #ff9800 100%) !important;
    padding: 10rem 0rem !important;
}

.justify-content-center {
    justify-content: center !important;
}

.text-white {
    color: #fff !important;
}

.main-content {
    position: relative;
}

.text-center {
    text-align: center !important;
}

.bg-secondary {
    background-color: #f7fafc !important;
}

.bg-transparent {
    background-color: transparent !important;
}

.ct-grid {
    stroke: rgba(255, 255, 255, 0.2);
    stroke-width: 1px;
    stroke-dasharray: 2px;
}

.ct-series-a .ct-point, .ct-series-a .ct-line, .ct-series-a .ct-bar, .ct-series-a .ct-slice-donut {
    stroke: rgba(255, 255, 255, 0.8);
}

.ct-label.ct-horizontal.ct-end {
    -webkit-box-align: flex-start;
    -webkit-align-items: flex-start;
    -ms-flex-align: flex-start;
    align-items: flex-start;
    -webkit-box-pack: flex-start;
    -webkit-justify-content: flex-start;
    -ms-flex-pack: flex-start;
    justify-content: flex-start;
    text-align: left;
    text-anchor: start;
}

.ct-label {
    color: rgba(255, 255, 255, 0.7);
}

.ct-chart-line .ct-label, .ct-chart-bar .ct-label {
    display: block;
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
}

.ct-label {
    fill: rgba(0, 0, 0, 0.4);
    line-height: 1;
}

body {
    background-color: #e4e4e4;
    color: #3c4858;
    margin: 0;
}

blockquote footer:before, blockquote small:before {
    content: "\2014 \00A0";
}

small {
    font-size: 80%;
}

h1 {
    font-size: 3em;
    line-height: 1.15em;
}

h2 {
    font-size: 2.4em;
}

h3 {
    font-size: 1.825em;
    line-height: 1.4em;
    margin: 20px 0 10px;
}

h4 {
    font-size: 1.3em;
    line-height: 1.4em;
}

h5 {
    font-size: 1.25em;
    line-height: 1.4em;
